/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';

/**
 * Internal dependencies
 */
import {openNotification} from '../../functions';
import { loaderIcon } from '../../assets/icons';

export default function License() {
    let  msg='';
    let _localizeData = rv_dashboard_data.dashboardData || {}; // localize data

    const [inputValue, setInputValue] = useState('');
    const [loader, setLoader] = useState(false);
    const [status, setStatus] = useState( _localizeData?.license_data?.status || 'deactive');

    const handleButtonClick = async (event, action) => {
        event.preventDefault();
        setLoader(true);

        // Pass data to the API using wp.apiFetch
        wp.apiFetch({
            path: '/riovizualpro/v2/license_trigger',
            method: 'POST',
            data: {
                action,
                inputValue,
            },
        })
        .then((response) => {
            setLoader(false);
            setStatus(response.status);
            let msg = response.message;
            let notice_status = response.status === 'active' ? 'success' : 'error';
            // show notice
            openNotification( notice_status, msg, '');

            window.location.reload();
        })
        .catch((error) => {
            setLoader(false);
        });
    };

    return (
        <div className="rv_dash-settings-page">
            <div className="rv_dash-lisence-box">
                <div className="rv_dash-lisence-status">
                    <h5>{__('Plugin License')}</h5>
                    <div className="rv_dash-lisence-status-btn">
                        {__('Status: ')}
						<span className={classnames(
                            status === 'active' ? 'rv_dash-lisence-active' : 'rv_dash-lisence-inactive'
                        )}>{__(status === 'active' ? 'Active' : 'License is not active')}</span>
                    </div>
                </div>
                <form id="apiKeyCheckForm" method="POST" >
                    <div className="">
                        <div className="form-row">
                            <label for="api_key" className="form-label">{__('License API Key:')}</label>
                            <input
                                type="text"
                                name="rv_license_key"
                                placeholder={ __( status === 'active' ? '🔑  Your license is active.' : 'Paste your license key here.' ) }
                                value={status === 'active' ? '' : inputValue}
                                disabled={status === 'active'}
                                onChange={(e) => setInputValue(e.target.value)} 
                            />
                        </div>
                        { <small>{__(msg)}</small>  }
                    </div>
                    <button
                    className={classnames(
                        'rv_dash-lisence-submit-btn',
                        {'rv_key_btn_loader': loader}
                    )}
                    onClick={(event) => handleButtonClick(event, status === 'active' ? 'deactive' : 'active')}
                    >
                    { __(status === 'active' ? 'Deactivate License' : 'Activate') }
                    { loader && loaderIcon }
                    </button>
                </form>
            </div>
        </div>
    )
}


